  <template>
    <div>
      <el-row>
        <el-col :span="5">
          <el-input
          placeholder=""
          icon="search"
          v-model="searchContent">
        </el-input>
      </el-col>

      <el-col :span="3">
        <el-button @click="handleSearch"
        >
      </el-button>
    </el-col>
    <!-- 24span5 -->
    <el-col :span="4">
      <!--  -->
      <el-button-group>
        <el-button type="primary" icon="plus" @click="addDialogVisible=true">
        </el-button>

        <el-button type="primary" icon="edit" @click="showEditDialogVisible">
        </el-button>

        <el-button type="primary" icon="delete" @click="delConfirmation">
        </el-button>
      </el-button-group>
    </el-col>

    <el-col :span="4">
      <el-button @click="resetPwdConfirmation">
      </el-button>
    </el-col>
  </el-row>

  <el-row type="flex">
    <el-col :span="15">
      <!--  -->
      <enterprise-information-table 
      :enterpriseTableData="realEstateEnTableData"
      @handleSelectionChange="handleSelectionChange"></enterprise-information-table>
    </el-col>
  </el-row>
  
  <el-row>
    <el-col :span="8">
      <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="totalNum">
    </el-pagination>
  </el-col>
</el-row>

<!-- Add Dialog -->
<el-dialog title="" :visible.sync="addDialogVisible">
  <el-form>
    <enterprise-information
    :enterprise="enterpriseForAddForm"
    ></enterprise-information>
  </el-form>

  <div slot="footer">
    <el-button @click="enterpriseForAddForm={}"> </el-button>

    <el-button @click="cancelAdd"> </el-button>

    <el-button type="primary" @click="submitAddForm"> </el-button>
  </div>
</el-dialog>
<!-- Add Dialog Finished-->

<!-- Edit Dialog-->
<el-dialog title="" :visible.sync="editDialogVisible">
  <div>
    <enterprise-information
    :enterprise="enterpriseForUpdateForm"
    ></enterprise-information>
  </div>

  <div slot="footer">
    <el-button 
        @click="enterpriseForUpdateForm={name:'',username:''}"
    > </el-button>

    <el-button @click="editDialogVisible=false"> </el-button>

    <el-button type="primary" @click="submitUpdateForm"> </el-button>
  </div>
</el-dialog>
<!-- Edit Dialog Finished -->

<!-- Delete Dialog-->
<el-dialog title="" :visible.sync="delConfirmationDialogVisible">
  <el-row>
    <div>
      <label></label>
      <!--  -->
      <el-row>{{ }}</el-row>
      <el-row>
        <label v-for="row in tableSelectedRows">
          <el-row></el-row>
          {{row.name}}
        </label>
      </el-row>
    </div>
  </el-row>
  <div slot="footer">
    <el-button @click="delConfirmationDialogVisible = false"> </el-button>

    <el-button type="primary" @click="deleteEnterprise"> </el-button>
  </div>
</el-dialog>
<!-- Delete Dialog Finished -->

<!-- Reset Password Dialog-->
<el-dialog title="" :visible.sync="resetPwdConfirmationDialogVisible">

  <el-form>
    <label></label>
  </el-form>
  <div slot="footer">
    <el-button @click="resetPwdConfirmationDialogVisible = false">  
    </el-button>

    <el-button type="primary" @click="handleResetPwd">  
    </el-button>
  </div>
</el-dialog>
<!-- Edit Dialog Finished -->

<!-- ref -->
<msg-dialog ref="msgDialog"></msg-dialog>
</div>
</template>

<script>
  import enterpriseInformation from './enterpriseInformation'
  import msgDialog from '../common/msgDialog'
  import enterpriseInformationTable from '../bizCommon/enterpriseInformationTable'

  export default {
    data() {
      return {
        pageSize:5,
        currentPage:1,
        totalNum:0,
        //
        realEstateEnTableData: [],
        //
        searchContent: '',
        searchContentFinal:'',
        //
        addDialogVisible: false,
        //
        editDialogVisible: false,
        //
        msgDialogVisible: false,
        //
        delConfirmationDialogVisible: false,
        //
        resetPwdConfirmationDialogVisible: false,
        //
        enterpriseForAddForm: {name: '', username: '',password:''},
        enterpriseForUpdateForm: {name: '', username: ''},
        enterpriseForUpdate:'',
        //
        tableSelectedRows: [],
        //
        isMultiRowsSelected: false,
        //
        ids: []
      }
    },
    methods: {
      handleSizeChange(newPageSize){
        this.pageSize=newPageSize;
        this.findAllRealEstateEns();
      },
      handleCurrentChange(newPage){
        this.currentPage = newPage;
        this.findAllRealEstateEns();
      },
      handleSearch() {
        this.searchContentFinal = this.searchContent
        this.findAllRealEstateEns()
      },
      //
      handleSelectionChange(selectedRows) {
        //
        this.tableSelectedRows = selectedRows
      },
      //
      showEditDialogVisible() {
        if (this.tableSelectedRows.length == 1) {
          this.editDialogVisible = true;
          this.enterpriseForUpdate=this.tableSelectedRows[0]
          //
          this.enterpriseForUpdateForm.name = this.tableSelectedRows[0].name
          this.enterpriseForUpdateForm.username = this.tableSelectedRows[0].username
        } else if (this.tableSelectedRows.length == 0) {
          this.$refs.msgDialog.confirm("");
        } else {
          this.$refs.msgDialog.confirm("")
        }
      },
      //
      submitAddForm() {
        if(!this.enterpriseForAddForm.username||!this.enterpriseForAddForm.name){
          this.$refs.msgDialog.confirm("")
        }else{
          this.addDialogVisible = false;
          this.enterpriseForAddForm.password = this.enterpriseForAddForm.username
          var url = this.HOST + "/addRealEstateEn"
          this.$http.post(url, this.enterpriseForAddForm).then(response => {
            this.findAllRealEstateEns();
            this.$refs.msgDialog.notify("")
          }).catch(error => {
            this.$refs.msgDialog.confirm("")
          })
          this.enterpriseForAddForm = {}
        }
      },
      //
      submitUpdateForm() {
        if(!this.enterpriseForUpdateForm.username||!this.enterpriseForUpdateForm.name){
          this.$refs.msgDialog.confirm("")
        }else{
          this.editDialogVisible = false;
          this.enterpriseForUpdate.name=this.enterpriseForUpdateForm.name
          this.enterpriseForUpdate.username=this.enterpriseForUpdateForm.username
          var url = this.HOST + "/updateRealEstateEn"
          this.$http.put(url, this.enterpriseForUpdate).then(response => {
            this.findAllRealEstateEns();
            this.$refs.msgDialog.notify("")
          }).catch(error => {
            this.$refs.msgDialog.confirm("")
          })
        }
      },
      //
      delConfirmation() {
        if (this.tableSelectedRows.length == 0) {
          this.$refs.msgDialog.confirm("")
        } else {
          this.delConfirmationDialogVisible = true
        }
      },
      //id,
      deleteEnterprise() {
        this.delConfirmationDialogVisible = false
        var i = 0;
        this.tableSelectedRows.forEach(item => {
          this.ids[i] = item.id;
          i = i + 1
        });
        this.delRecord()
      },
      //
      delRecord() {
        var url = this.HOST + "/deleteRealEstateEns?ids=" + this.ids
        this.$http.delete(url).then(response => {
          this.findAllRealEstateEns();
          this.$refs.msgDialog.notify("")
        }).catch(response => {
          this.$refs.msgDialog.confirm("")
        })
      },
      //
      resetPwdConfirmation() {
        if (this.tableSelectedRows.length == 1) {
          this.resetPwdConfirmationDialogVisible = true
        } else if (this.tableSelectedRows == 0) {
          this.$refs.msgDialog.confirm("")
        } else {
          this.$refs.msgDialog.confirm("")
        }
      },
      //
      handleResetPwd: function() {
        this.resetPwdConfirmationDialogVisible=false
        var url = this.HOST + "/resetPsd?id="+this.tableSelectedRows[0].id
        this.$http.post(url).then(response=>{
          this.$refs.msgDialog.notify(response.data.username +"")
          this.findAllRealEstateEns()
        }).catch(response=>{
          this.$refs.msgDialog.confirm("")
        })
      },
      //
      cancelAdd() {
        //
        this.addDialogVisible = false;
        this.enterpriseForAddForm = {name: '', username: ''}
      },
      //
      findAllRealEstateEns() {
        var url = this.HOST + "/queryRealEstateEnByName?nameQuery="+this.searchContentFinal+"&page="+this.currentPage+"&rows="+this.pageSize
        //
        // 
        this.$http.get(url).then(response => {
          this.realEstateEnTableData = response.data.rows;
          this.totalNum = response.data.total;
        }).catch(error => {
          this.$refs.msgDialog.confirm("")
        })
      }
    },
    //watch
    watch: {},
    //
    created() {
      this.findAllRealEstateEns()
    },
    components: {
      enterpriseInformation,
      msgDialog,
      enterpriseInformationTable
    }
  }
</script>



