<template>
	<div>
		<el-row>
			<el-col :span="5">
				<h4></h4>
			</el-col>
		</el-row>

		<el-row>
			<el-col :span="4">
				<el-input v-model="searchContent" placeholder=""></el-input>
			</el-col>
			<el-col :span="3">
				<el-button @click="searchNonTraditionalData"></el-button>
			</el-col>
			<el-col :span="3">
				<el-button @click="checkNontraditionalInfo"></el-button>
			</el-col>
		</el-row>
		<el-table
		    :data="nonTraditionalCheckTableData"
		    border
		    tooltip-effect="dark"
		    style="width:100%"
		    highlight-current-row
		    @row-click="handleSelectionChange">

		    <el-table-column
		      label=""
		      prop="name"
		      show-overflow-tooltip>
		    </el-table-column>

		    <el-table-column
		      label=""
		      prop="enterpriseType"
		      show-overflow-tooltip>
		    </el-table-column>

		    <el-table-column
		      label=""
		      prop="creditCode"
		      show-overflow-tooltip>
		    </el-table-column>

		    <el-table-column
		      label=""
		      prop="organizationCode"
		      show-overflow-tooltip>
		    </el-table-column>

		    <el-table-column
		      label=""
		      prop="city"
		      show-overflow-tooltip>
		    </el-table-column>

		    <el-table-column
		      label=""
		      prop="legalRepresentative"
		      show-overflow-tooltip>
		    </el-table-column>

		    <el-table-column
		      prop="checkedStatus.state"
		      label="">
		    </el-table-column>
	  	</el-table>
	  	<!--  -->
	  	<el-pagination
	      @size-change="handleSizeChange"
	      @current-change="handleCurrentChange"
	      :current-page="currentPage"
	      :page-sizes="[5, 10, 15, 20]"
	      :page-size="pageSize"
	      layout="total, sizes, prev, pager, next, jumper"
	      :total="total">
	    </el-pagination>
	    <!--  -->
	  	<msg-dialog ref="nonTraditionalCheckMsg"></msg-dialog>
	  	<!--  -->
	  	<el-dialog
		  	title=""
		  	:visible.sync="checkStatusDialogVisable"
		  	size="tiny">
		  	<el-select v-model="checkStatusId" placeholder="">
			    <el-option
			      v-for="item in checkStatus"
			      :key="item.id"
			      :label="item.state"
			      :value="item.id">
			    </el-option>
			 </el-select>
		  	<span slot="footer" class="dialog-footer">
		    <el-button @click="checkStatusDialogVisable = false"> </el-button>
		    <el-button type="primary" @click="checkStatusUpdate"> </el-button>
		  </span>
		</el-dialog>
	</div>
</template>

<script>
import msgDialog from '../common/msgDialog'
	export default{
		data(){
			return{
				//
				nonTraditionalCheckTableData:[],
				//
				checkStatusDialogVisable:false,
				//
				checkStatus:[],
				//Id
				checkStatusId:'',
				//id
				currentRowId:'',
				//
				//
				currentPage:1,
				//
				pageSize:5,
				//
				total:0,
				//
				searchContent:'',
				//
				searchContentFinal:''


			}
		},
		//
		created(){
			//
			this.findNonTraditionalInfo()
			//
			var url = this.HOST + '/findAllCheckedStates'
			this.$http.get(url).then(response=>{
				this.checkStatus = response.data
			})
		},
		methods:{
			//
			checkNontraditionalInfo(){
				if(this.currentRowId==''){
					this.$refs.nonTraditionalCheckMsg.confirm("")
				}else{
					this.checkStatusDialogVisable=true
				}
			},
			//
			handleSizeChange(newPageSize){
				this.pageSize=newPageSize
				this.findNonTraditionalInfo()
			},
			//
			handleCurrentChange(newPage){
				this.currentPage = newPage
				this.findNonTraditionalInfo()
			},
			//
			findNonTraditionalInfo(){
				var url = this.HOST + "/displayOrganizations?page="+this.currentPage+"&rows="+this.pageSize
				this.$http.get(url).then(response=>{
					this.nonTraditionalCheckTableData = response.data.rows
					this.total = response.data.total
					//
					this.nonTraditionalCheckTableData.forEach(item=>{
						if(!item.checkedStatus){
							item.checkedStatus={id:'',no:'',state:""}
						}
					})
					
				}).catch(error=>{
					this.$refs.nonTraditionalCheckMsg.confirm("")
				})
			},
			//
			handleSelectionChange(currentRow){
				//id
				this.currentRowId=currentRow.id
				//id
				this.checkStatusId=currentRow.checkedStatus.id
			},
			//
			checkStatusUpdate(){
				this.checkStatusDialogVisable=false
				var url=this.HOST+"/updateTraditionalEn?id="+this.currentRowId+"&checkedStatusId="+this.checkStatusId
				this.$http.post(url).then(response=>{
					if(this.searchContentFinal){
						var url = this.HOST + "/queryOrganizations?queryName="+this.searchContentFinal+"&rows="+this.pageSize+"&page="+this.currentPage
						this.$http.get(url).then(response=>{
							this.nonTraditionalCheckTableData = response.data.rows
							this.total = response.data.total
							//
							this.nonTraditionalCheckTableData.forEach(item=>{
								if(!item.checkedStatus){
									item.checkedStatus={id:'',no:'',state:""}
								}
							})
						}).catch(error=>{
							this.$refs.nonTraditionalCheckMsg.confirm("")
						})
					}else{
						this.findNonTraditionalInfo()
					}
					
					this.$refs.nonTraditionalCheckMsg.notify("")
				}).catch(error=>{
					this.$refs.nonTraditionalCheckMsg.confirm("")
				})
				this.checkStatusId=''
				this.currentRowId=''
			},
			//
			searchNonTraditionalData(){
				this.searchContentFinal = this.searchContent
				var url = this.HOST + "/queryOrganizations?queryName="+this.searchContentFinal+"&rows="+this.pageSize+"&page="+this.currentPage
				this.$http.get(url).then(response=>{
					this.nonTraditionalCheckTableData = response.data.rows
					this.total = response.data.total
					//
					this.nonTraditionalCheckTableData.forEach(item=>{
						if(!item.checkedStatus){
							item.checkedStatus={id:'',no:'',state:""}
						}
					})
				}).catch(error=>{
					this.$refs.nonTraditionalCheckMsg.confirm("")
				})
			}
		},
		components:{
			msgDialog
		}
	}
</script>


