<template>
<div>
    <el-form :model="machineryEnInformation" label-width="180px">
    <!--  -->
        <part-basic-info :basicInfoData="machineryEnInformation"
        ref="partBasicInfo" @checkOthers="submit"></part-basic-info>
        <el-row>
            <el-col :span="7">
                <label></label>
            </el-col>
        </el-row>
        <el-row></el-row>
  
    </el-form>
    <!--  -->
    <all-type-basic-info :basicInfoData="machineryEnInformation"
        @submitForm="submitBasicInfoDialog"
    ></all-type-basic-info>
    <!--  -->
    <msg-dialog ref="msgForSubmit"></msg-dialog>
    <!--  -->
    <confirmation-dialog 
        :submitBasicInfoDialogVisible="submitBasicInfoDialogVisible"
        @handleSubmit="handleSubmit"
        @cancle="submitBasicInfoDialogVisible=false"
    ></confirmation-dialog>
</div>
</template>
<script>
import partBasicInfo from './publicComponents/partBasicInfo'
import allTypeBasicInfo from './publicComponents/allTypeBasicInfo'
import msgDialog from '../common/msgDialog'
import confirmationDialog from './publicComponents/confirmationDialog'
export default {
    //
    created(){
        //
        var userNameUrl = this.HOST + '/returnUserInfo'
        this.$http.get(userNameUrl).then(response=>{
            this.machineryEnInformation = response.data
        }).catch(error=>{
            this.$refs.msgForSubmit.confirm("")
        })
    },
    data: function() {
        return {
            //
            machineryEnInformation: {},
            //
            submitBasicInfoDialogVisible:false
        }
    },
    methods: {
        //
        handleSubmit() {
            //
            this.submitBasicInfoDialogVisible=false
            var url = this.HOST + '/updateMachineryEn'
            this.$http.put(url,this.machineryEnInformation).then(response=>{
                this.$refs.msgForSubmit.notify("")
            }).catch(error=>{
                this.$refs.msgForSubmit.confirm("")
            })
        },
        submitBasicInfoDialog(){
            this.$refs.partBasicInfo.check();
        },
        submit(){
            this.submitBasicInfoDialogVisible=true
        } 
    },
    components: {
        partBasicInfo,
        allTypeBasicInfo,
        msgDialog,
        confirmationDialog
    }
}

</script>



