<template>
	<div>
		<el-row>
			<el-col :span="26">
				<label></label>
			</el-col>
		</el-row>
		<el-row></el-row>
		<el-row>
			<el-col :span="4">	
				<el-input
				    v-model="selectedYearForProjectStatus"
				    align="right"
				    size="small"
				    type="number"
				    min="1"
				    placeholder="">
			 	</el-input>
			</el-col>
			<el-col :span="1"></el-col>		 	
			<el-col :span="4">
			 	<el-input
					placeholder=""
					align="right"
					size="small"
					type="number"
					max="4"
					min="1"
					v-model="selectedQuarterForProjectStatus"
			 	></el-input>		 	
			 	
			</el-col>
			<el-col :span="1"></el-col>
			<el-col :span="3">
				<el-button @click="searchProjectStatusInfo" size="small"></el-button>
			</el-col>
		</el-row>
		<el-row>
			<el-table
			:data="projectStatusInfoData"
			border
			style="width:100%">
				<el-table-column
				label=""
				type="index"
				header-align="center"
				width="100"
				>
				</el-table-column>
				<el-table-column
				prop="description"
				label=""
				header-align="center">
				</el-table-column>
				<el-table-column
				prop="number"
				label=""
				header-align="center"
				>
				</el-table-column>
			</el-table>
		</el-row>
		<el-row></el-row>
		<el-row>
			<label>
				1
			</label>
		</el-row>
		<el-row></el-row>
		<el-row>
			<el-col :span="4">	
				<el-input
				    v-model="selectedYearForIndustryInfoOne"
				    align="right"
				    size="small"
				    type="number"
				    min="1"
				    placeholder="">
			 	</el-input>
			</el-col>
			<el-col :span="1"></el-col>		 	
			<el-col :span="4">
			 	<el-input
					placeholder=""
					align="right"
					size="small"
					type="number"
					max="4"
					min="1"
					v-model="selectedQuarterForIndustryInfoOne"
			 	></el-input>		 	
			 	
			</el-col>
			<el-col :span="1"></el-col>
			<el-col :span="3">
				<el-button @click="searchIndustryInfoOne" size="small"></el-button>
			</el-col>
		</el-row>
		<el-row>
			<el-table
			:data="industryInfoDataOne"
			border
			style="width:100%">
				<el-table-column
				label=""
				prop="formDes"
				header-align="center"
				width="300"
				>
				</el-table-column>
				<el-table-column
				prop="projectNum"
				label=""
				header-align="center">
				</el-table-column>
				<el-table-column
				prop="area"
				label=""
				header-align="center"
				>
				</el-table-column>
			</el-table>
		</el-row>
		<el-row></el-row>
		<el-row>
			<label>
				2
			</label>
		</el-row>
		<el-row></el-row>
		<el-row>
			<el-col :span="4">	
				<el-input
				    v-model="selectedYearForIndustryInfoTwo"
				    align="right"
				    size="small"
				    type="number"
				    min="1"
				    placeholder="">
			 	</el-input>
			</el-col>
			<el-col :span="1"></el-col>		 	
			<el-col :span="4">
			 	<el-input
					placeholder=""
					align="right"
					size="small"
					type="number"
					max="4"
					min="1"
					v-model="selectedQuarterForIndustryInfoTwo"
			 	></el-input>		 	
			 	
			</el-col>
			<el-col :span="1"></el-col>
			<el-col :span="3">
				<el-button @click="searchIndustryInfoTwo" size="small"></el-button>
			</el-col>
		</el-row>
		<el-row>
			<el-table
			:data="industryInfoDataTwo"
			border
			style="width:100%">
				<el-table-column
				label=""
				prop="description"
				header-align="center"
				width="300"
				>
				</el-table-column>
				<el-table-column
				prop="number"
				label=""
				header-align="center">
				</el-table-column>
				<el-table-column
				prop="area"
				label=""
				header-align="center"
				>
				</el-table-column>
			</el-table>
		</el-row>
		<msg-dialog ref="industryMsg"></msg-dialog>
	</div>
</template>
<script>
import msgDialog from '../common/msgDialog'
	export default{
		data(){
			return{
				//
				projectStatusInfoData:[],
				industryInfoDataOne:[],
				industryInfoDataTwo:[],

				//
				initSelectedYear:'',
				//
				initSelectedQuarter:'',

				selectedYearForProjectStatus:'',
				selectedQuarterForProjectStatus:'',
				selectedYearForIndustryInfoOne:'',
				selectedQuarterForIndustryInfoOne:'',
				selectedYearForIndustryInfoTwo:'',
				selectedQuarterForIndustryInfoTwo:'',

			}
		},
		created(){
			var date = new Date()
			var currMonth=date.getMonth()
			this.initSelectedYear=date.getFullYear()
			this.initSelectedQuarter=Math.floor( ( currMonth % 3 == 0 ? ( currMonth / 3 ) : ( currMonth / 3 + 1 ) ) )
			this.selectedYearForProjectStatus=this.initSelectedYear
			this.selectedYearForIndustryInfoOne=this.initSelectedYear
			this.selectedYearForIndustryInfoTwo=this.initSelectedYear

			this.selectedQuarterForProjectStatus=this.initSelectedQuarter
			this.selectedQuarterForIndustryInfoOne=this.initSelectedQuarter
			this.selectedQuarterForIndustryInfoTwo=this.initSelectedQuarter
			this.getAllProjectStatusInfo()
			this.getAllIndustryInfoOne()
			this.getAllIndustryInfoTwo()
		},
		methods:{
			getAllProjectStatusInfo(){
				var url = this.HOST + "/queryQuarterProject?year="+this.selectedYearForProjectStatus+"&quarter="+this.selectedQuarterForProjectStatus
				this.$http.get(url).then(response=>{
					this.projectStatusInfoData=response.data
					this.$refs.industryMsg.notify("")
				}).catch(error=>{
					this.$refs.industryMsg.confirm("")
				})
			},
			getAllIndustryInfoOne(){
				var url = this.HOST + "/queryQuarterFormProject?year="+this.selectedYearForIndustryInfoOne+"&quarter="+this.selectedQuarterForIndustryInfoOne
				this.$http.get(url).then(response=>{
					this.industryInfoDataOne=response.data
					this.$refs.industryMsg.notify("")
				}).catch(error=>{
					this.$refs.industryMsg.confirm("")
				})
			},
			getAllIndustryInfoTwo(){
				var url = this.HOST + "/queryQuarterUnitEngineering?year="+this.selectedYearForIndustryInfoTwo+"&quarter="+this.selectedQuarterForIndustryInfoTwo
				this.$http.get(url).then(response=>{
					this.industryInfoDataTwo=response.data
					this.$refs.industryMsg.notify("")
				}).catch(error=>{
					this.$refs.industryMsg.confirm("")
				})
			},
			searchProjectStatusInfo(){
				this.getAllProjectStatusInfo()
			},
			searchIndustryInfoOne(){
				this.getAllIndustryInfoOne()
			},
			searchIndustryInfoTwo(){
				this.getAllIndustryInfoTwo()
			}
		},
		components:{
			msgDialog
		}
	}
</script>


