<template>
    <div>
        <el-row>
            <el-col :span="6">
                <el-input icon="search" v-model="searchContent" placeholder="" class="search-input">
                </el-input>
                </el-col>
                <el-col :span="6">
                    <el-date-picker
                      v-model="searchTime"
                      type="daterange"
                      placeholder="">
                    </el-date-picker>
                </el-col>
                <el-col :span="2">
                    <el-button @click="handleSearch"></el-button>
                </el-col>
        </el-row>
        <el-row>
            <el-col :span="20" :push="6">
                <el-button @click="rebutAudit"></el-button>
                <el-button type="primary" @click="passAudit"></el-button>
            </el-col>
        </el-row>
        <el-row>
            <el-col>
                <el-table
                :data="machineryEnIndustrializationData"
                stripe
                border
                style="width:75%"
                highlight-current-row
                @current-change="handleSelectionChange">
                    <el-table-column
                      label=""
                      type="index">
                    </el-table-column>
                    <el-table-column label="" prop="machineryEn.name" > 
                    </el-table-column>
                    <el-table-column label="" prop="declareTime" > 
                    </el-table-column>
                    <el-table-column label="" prop="integralWall" > 
                    </el-table-column>
                    <el-table-column label="" prop="specialTransportEquipment" > 
                    </el-table-column>
                    <el-table-column label="" prop="specialConstructionEquipment" > 
                    </el-table-column>
                    <el-table-column label="" prop="checkedStatus.state" > 
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="8">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="currentPage"
                  :page-sizes="[5, 10, 15, 20]"
                  :page-size="pageSize"
                  layout="total, sizes, prev, pager, next, jumper"
                  :total="totalNumber">
                </el-pagination>
            </el-col>
        </el-row>
    <msg-dialog ref="msgDialog"></msg-dialog>
    </div>
</template>
<script>
    import msgDialog from '../common/msgDialog'
    export default{
        data(){
            return{
                //
                searchContent:'',
                //
                searchTime:'',
                //
                currentPage:1,
                //
                currentPageSize:5,
                totalNumber:0,
                //
                auditDialogVisible: false,
                //
                selectedRows:null,
                //id
                id:'',
                //
                machineryEnIndustrializationData:[],
                //
                machineryEnTable:{integralWall:'',specialTransportEquipment:'',specialConstructionEquipment:'',checkedStatus:{id:''},submit:true} 
            }
        
        },
        methods:{
            //
            handleSearch(){
                if(this.searchContent==''|| this.searchTime ==''){
                    this.$refs.msgDialog.confirm("")
                }else{
                    var startTime = this.moment(this.searchTime[0]).format('YYYY-MM-DD')
                    var endTime = this.moment(this.searchTime[(this.searchTime.length)-1]).format('YYYY-MM-DD')
                    var url = this.HOST+'/queryMachineryEnIn?machineryEnName='+this.searchContent+'&startTime='+startTime+'&endTime='+endTime+'&page='+this.currentPage+'&rows='+this.currentPageSize
                    this.$http.get(url).then(response=>{
                        this.machineryEnIndustrializationData = response.data.rows
                        this.totalNumber = response.data.total
                    }).catch(error=>{
                        this.$refs.msgDialog.confirm("")
                    })
                }
            },
            //
            handleSelectionChange(selectedRows){
                this.selectedRows = selectedRows
                this.showMachineryEnIndustrializationData()
            },
            //
            findAllMachineryEnIndustrializationsTable(){
                var url = this.HOST + '/displayAllMachineryEnIndustrializationsBySubmit?page='+this.currentPage+"&rows="+this.currentPageSize
                this.$http.get(url).then(response=>{
                    this.machineryEnIndustrializationData = response.data.rows
                    this.totalNumber = response.data.total
                }).catch(error=>{
                    this.$refs.msgDialog.confirm("")
                })
            },
            //
            handleSizeChange(newPageSize){
                this.currentPageSize = newPageSize
                this.findAllMachineryEnIndustrializationsTable()
            },
            //
            handleCurrentChange(newPage){
                this.currentPage = newPage
                this.findAllMachineryEnIndustrializationsTable()
            },
           //
            showMachineryEnIndustrializationData() {
                this.machineryEnTable = this.selectedRows
            },
            //
            passAudit(){
                this.machineryEnTable.checkedStatus={id:1}
                var url = this.HOST+'/updateMachineryEnIndustrialization'
                this.$http.put(url,this.machineryEnTable).then(response=>{
                    this.findAllMachineryEnIndustrializationsTable()
                    this.$refs.msgDialog.notify("")
                }).catch(error => {
                    this.$refs.msgDialog.confirm("")
                })
            },
            //
             rebutAudit(){
                this.machineryEnTable.checkedStatus={id:2}
                var url = this.HOST+'/updateMachineryEnIndustrialization'
                this.$http.put(url,this.machineryEnTable).then(response=>{
                    this.findAllMachineryEnIndustrializationsTable()
                    this.$refs.msgDialog.notify("")
                }).catch(error => {
                    this.$refs.msgDialog.confirm("")
                })
             }
        },
        created() {
            this.findAllMachineryEnIndustrializationsTable()
        },
        components:{
            msgDialog
        }
    }
</script>


