<template>
    <div>
        <!--  -->
        <el-row>
            <el-col :span="5">
                <el-input icon="search" v-model="searchContent" placeholder="" class="search-input">
                </el-input>
            </el-col>
            <el-col :span="3">
                <el-button @click="handleNameSearch"></el-button>
            </el-col>
        </el-row>
        <!--  -->
        <el-row>
            <el-col :span="10">
                <el-table
                :data="machineryEnTable"
                stripe
                border
                highlight-current-row
                style="width:75%"
                @current-change="handleSelectionChange">
                    <el-table-column
                      label=""
                      type="index"
                      width="55">
                    </el-table-column>
                    <el-table-column label="" prop="name" ></el-table-column>
                </el-table>
                <!--  -->
                <el-row>
                    <el-col :span="8">
                        <el-pagination
                          @size-change="handleSizeChange"
                          @current-change="handleCurrentChange"
                          :current-page="currentPage"
                          :page-sizes="[5,10,15,20,]"
                          :page-size="currentPageSize"
                          layout="total, sizes, prev, pager, next, jumper"
                          :total="totalNumber">
                        </el-pagination>
                    </el-col>
                </el-row>
            </el-col>
            <!--  -->
            <el-col :span="14">
                <!--  -->
                <el-row>
                    <el-col :span="8">
                        <el-date-picker
                          v-model="searchYear"
                          align="right"
                          type="year"
                          placeholder="">
                        </el-date-picker>
                    </el-col>
                    <el-col :span="2">
                        <el-input type="number" max="4" min="1" v-model.number="searchQuarter"></el-input>
                    </el-col>
                    <el-col :span="4">
                        <el-button @click="handleYearSearch"></el-button>
                    </el-col>
                </el-row>
                <!--  -->
                <el-row>
                    <el-col>
                        <el-row>
                            <el-col :span="6"></el-col>
                            <el-col :span="5"><span></span></el-col>
                        </el-row>
                        <el-row >
                            <el-col :span="6"></el-col>
                            <el-col :span="5">{{this.machineryEnIndustrializationData.integralWall}}/</el-col>
                        </el-row>
                        <el-row >
                            <el-col :span="6"></el-col>
                            <el-col :span="5">{{this.machineryEnIndustrializationData.specialTransportEquipment}}/</el-col>
                        </el-row>
                        <el-row >
                            <el-col :span="6"></el-col>
                            <el-col :span="5">{{this.machineryEnIndustrializationData.specialConstructionEquipment}}/</el-col>
                        </el-row>
                    </el-col>
                </el-row>
            </el-col>
        </el-row>
         <msg-dialog ref="msgDialog"></msg-dialog>
    </div>
</template>
<script>
import msgDialog from '../common/msgDialog'
    export default{
        data(){
            return{
                //
                machineryEnTable:[],
                //
                currentPage:1,
                //
                currentPageSize:5,
                //
                totalNumber:0,
                //
                searchContent:'',
                //
                searchYear:'',
                //
                searchQuarter:'',
                //
                selectedRows:null,
                //ID
                enId:'',
                //
                machineryEnIndustrializationData:{integralWall:'',specialTransportEquipment:'',specialConstructionEquipment:'',}
            }
        },
        methods:{
            //
            handleNameSearch(){
                if(this.searchContent==''){
                    this.$refs.msgDialog.confirm("")
                }else{
                    var url = this.HOST+'/queryMachineryEnByName?nameQuery='+this.searchContent+'&page='+this.currentPage+'&rows='+this.currentPageSize
                    this.$http.get(url).then(response=>{
                        this.machineryEnTable = response.data.rows
                        this.totalNumber = response.data.total
                    }).catch(error=>{
                        this.$refs.msgDialog.confirm("")
                    })
                }
            },
            //
            handleYearSearch(){
                if(this.searchYear==''|| this.searchQuarter ==''){
                    this.$refs.msgDialog.confirm("")
                }else{
                    var year = this.moment(this.searchYear).format('YYYY')
                    var url = this.HOST+'/queryQuarterMachineryEnIn?enId='+this.enId+'&year='+year+'&quarter='+this.searchQuarter
                    this.$http.get(url).then(response=>{
                        this.machineryEnIndustrializationData = response.data
                    }).catch(error=>{
                        this.$refs.msgDialog.confirm("")
                    })
                }
            },
            //
            findAllMachineryEns(){
                var url = this.HOST + '/displayAllMachineryEns?page='+this.currentPage+"&rows="+this.currentPageSize
                this.$http.get(url).then(response=>{
                    this.machineryEnTable = response.data.rows
                    this.totalNumber = response.data.total
                }).catch(error=>{
                    this.$refs.msgDialog.confirm("")
                })
            },
            //
            handleSizeChange(newPageSize){
                this.currentPageSize = newPageSize
                this.findAllMachineryEns()
            },
            //
            handleCurrentChange(newPage){
                this.currentPage = newPage
                this.findAllMachineryEns()
            },
            //
            handleSelectionChange(selected){
                this.selectedRows = selected
                this.enId = this.selectedRows.id
            },
    },

        created(){
            this.findAllMachineryEns()
        },
        components:{
            msgDialog
        }
    }
</script>



