<template>
<div>
	<el-form ref="checkForm" :model="unitEngineeringDialog" :rules="rules">
		<el-form-item label=""prop="name">
	        <el-input v-model="unitEngineeringDialog.name">
	        </el-input>
	    </el-form-item>
	    <el-form-item label=""prop="startingTime">
			    <el-date-picker
			      v-model="unitEngineeringDialog.startingTime"
			      align="right"
			      type="date"
			      placeholder=""
			      >
			    </el-date-picker>
		</el-form-item>
	    <el-form-item label=""prop="constructionArea">
	        <el-input v-model.number="unitEngineeringDialog.constructionArea"><template slot="append"></template>
	        </el-input>
	    </el-form-item>
	    <el-form-item label=""prop="undergroundNum">
	        <el-input v-model.number="unitEngineeringDialog.undergroundNum"><template slot="append"></template>
	        </el-input>
	    </el-form-item>
	    <el-form-item label=""prop="overgroundNum">
	        <el-input v-model.number="unitEngineeringDialog.overgroundNum">
	        <template slot="append"></template></el-input>
	    </el-form-item>
	    <el-form-item label=""prop="engineeringCategory">
	        <el-input v-model="unitEngineeringDialog.engineeringCategory">
	        </el-input>
	    </el-form-item>
	    <el-form-item label=""prop="structureForm">
	    	<el-select v-model="unitEngineeringDialog.structureForm.id" placeholder="">
	            <el-option
	              v-for="item in structureForms"
	              :key="item.id"
	              :label="item.description"
	              :value="item.id">
	            </el-option>    
	        </el-select>
	    </el-form-item>
	    <el-form ref="checkForm2" :model="unitEngineeringDialog.engineeringIndustrialization" :rules="rules">
	    <el-form-item label=""prop="unitAssemblyRate">
	        <el-input v-model.number="unitEngineeringDialog.engineeringIndustrialization.unitAssemblyRate">
	        </el-input>
	    </el-form-item>
	    <el-form-item label=""prop="exteriorWallArea">
	        <el-input v-model.number="unitEngineeringDialog.engineeringIndustrialization.exteriorWallArea"><template slot="append"></template>
	        </el-input>
	    </el-form-item>
	    <el-form-item label=""prop="wallShadowArea">
	        <el-input v-model.number="unitEngineeringDialog.engineeringIndustrialization.wallShadowArea"><template slot="append"></template>
	        </el-input>
	    </el-form-item>
	    <el-form-item label=""prop="conArea">
	        <el-input v-model.number="unitEngineeringDialog.engineeringIndustrialization.conArea"><template slot="append"></template>
	        </el-input>
	    </el-form-item>
	    <el-form-item label=""prop="applicationStructureType">
	    	<el-select v-model="unitEngineeringDialog.engineeringIndustrialization.applicationStructureType.id" placeholder="">
	            <el-option
	              v-for="item in applicationStructureTypes"
	              :key="item.id"
	              :label="item.description"
	              :value="item.id">
	            </el-option>    
	        </el-select>       
	    </el-form-item>
	    <el-form-item label=""prop="floorScope">
	        <el-input v-model="unitEngineeringDialog.engineeringIndustrialization.floorScope">
	        </el-input>
	    </el-form-item>
	    </el-form>
	    <el-form-item label="">
	        <el-checkbox label="" v-model="unitEngineeringDialog.engineeringIndustrialization.frameworkShear.columnFs"></el-checkbox>
		    <el-checkbox label="" v-model="unitEngineeringDialog.engineeringIndustrialization.frameworkShear.beamFs"></el-checkbox>
		    <el-checkbox label="" v-model="unitEngineeringDialog.engineeringIndustrialization.frameworkShear.floorFs"></el-checkbox>
		    <el-checkbox label="" v-model="unitEngineeringDialog.engineeringIndustrialization.frameworkShear.stairsFs"></el-checkbox>
		    <el-checkbox label="" v-model="unitEngineeringDialog.engineeringIndustrialization.frameworkShear.exteriorWallFs"></el-checkbox>
		    <el-checkbox label="" v-model="unitEngineeringDialog.engineeringIndustrialization.frameworkShear.interiorWallFs"></el-checkbox>
		    <el-checkbox label="" v-model="unitEngineeringDialog.engineeringIndustrialization.frameworkShear.integralKitchenFs"></el-checkbox>
		    <el-checkbox label="" v-model="unitEngineeringDialog.engineeringIndustrialization.frameworkShear.integralToiletFs"></el-checkbox>
		    <el-checkbox label="" v-model="unitEngineeringDialog.engineeringIndustrialization.frameworkShear.solarEnergyFs"></el-checkbox>
	    </el-form-item>
	</el-form>
		<msg-dialog ref="msgDialog"></msg-dialog>
</div>
</template>
<script>
import msgDialog from '../common/msgDialog'
	export default{
		data(){
			var checkName=(rule,value,callback)=>{
				if(!value){
					callback(new Error(""))
				}else{
					callback()
				}	
			}
			var checkStartingTime=(rule,value,callback)=>{
				if(!value){
					callback(new Error(""))
				}else{
					callback()
				}	
			}
			var checkStructureForm=(rule,value,callback)=>{
				if(!value){
					callback(new Error(""))
				}else{
					callback()
				}	
			}
			var checkConstructionArea=(rule,value,callback)=>{
				if(!value){
					callback(new Error(""))
				}else
				callback();
			}
			var checkUndergroundNum=(rule,value,callback)=>{
				if(!value){
					callback(new Error(""))
				}else
				callback();
			}
			var checkApplicationStructureType=(rule,value,callback)=>{
				if(!value){
					callback(new Error(""))
				}else
				callback();
			}
			var checkOvergroundNum=(rule,value,callback)=>{
				if(!value){
					callback(new Error(""))
				}else
				callback();
			}
			var checkEngineeringCategory=(rule,value,callback)=>{
				if(!value){
					callback(new Error(""))
				}else
				callback();
			}
			var checkUnitAssemblyRate=(rule,value,callback)=>{
				if(!value){
					callback(new Error(""))
				}else
				callback();
			}
			var checkExteriorWallArea=(rule,value,callback)=>{
				if(!value){
					callback(new Error(""))
				}else
				callback();
			}
			var checkWallShadowArea=(rule,value,callback)=>{
				if(!value){
					callback(new Error(""))
				}else
				callback();
			}
			var checkConArea=(rule,value,callback)=>{
				if(!value){
					callback(new Error(""))
				}else
				callback();
			}
			var checkFloorScope=(rule,value,callback)=>{
				if(!value){
					callback(new Error(""))
				}else
				callback();
			}
      		return{
        		applicationStructureTypes:[],
        		structureForms:[],
        		rules:{
					name:[
						{validator:checkName,trigger:'change'&'blur'}
					],
					startingTime:[
						{validator:checkStartingTime,trigger:'change'&'blur'}
					],
					structureForm:[
						{validator:checkStructureForm,trigger:'change'&'blur'}
					],
					constructionArea:[
						{validator:checkConstructionArea,trigger:'blur'},
						{type:'number',message:'',trigger:'change'}
					],
					undergroundNum:[
						{validator:checkUndergroundNum,trigger:'blur'},
						{type:'number',message:'',trigger:'change'}
					],
					overgroundNum:[
						{validator:checkOvergroundNum,trigger:'blur'},
						{type:'number',message:'',trigger:'change'}
					],
					engineeringCategory:[
						{validator:checkEngineeringCategory,trigger:'change'&'blur'}
					],
					unitAssemblyRate:[
						{validator:checkUnitAssemblyRate,trigger:'blur'},
						{type:'number',message:'',trigger:'change'}
					],
					exteriorWallArea:[
						{validator:checkExteriorWallArea,trigger:'blur'},
						{type:'number',message:'',trigger:'change'}
					],
					wallShadowArea:[
						{validator:checkWallShadowArea,trigger:'blur'},
						{type:'number',message:'',trigger:'change'}
					],
					applicationStructureType:[
						{validator:checkApplicationStructureType,trigger:'change'&'blur'}
					],
					conArea:[
						{validator:checkConArea,trigger:'blur'},
						{type:'number',message:'',trigger:'change'}
					],
					floorScope:[
						{validator:checkFloorScope,trigger:'change'&'blur'}
					]
				}
      		}
    	},
    	methods: {
    		check(){
    			console.log("laillllll");
            	this.$refs.checkForm.validate((valid)=>{
                	if(valid){
                    	this.check2()
                	}else{
                    	this.$refs.msgDialog.confirm("")
                	}
            	})
            
        	},
        	check2(){
    			console.log("laillllll");
            	this.$refs.checkForm2.validate((valid)=>{
                	if(valid){
                    	this.$emit('submitUpdateForm')
                	}else{
                    	this.$refs.msgDialog.confirm("")
                	}
            	})
            
        	},
    		findAllApplicationStructureTypes() {
	        	this.$http.get(this.HOST + "/findAllApplicationStructureTypes").then(response => {
	          	this.applicationStructureTypes = response.data;
	        	}).catch(error => {
	          	this.$refs.msgDialog.confirm("")
	        	})
	      	},
	       	findAllStructureForms() {
        		this.$http.get(this.HOST + "/findAllStructureForms").then(response => {
          		this.structureForms = response.data;
        		}).catch(error => {
          		this.$refs.msgDialog.confirm("")
        		})
      		}
    	},
		props:['unitEngineeringDialog'],
		created() {
      		this.findAllApplicationStructureTypes()
      		this.findAllStructureForms()
    	},
    	components: {
      			msgDialog     
    	}
	}
</script>


