<template>
<div id="body">
	<el-row>
		<el-button @click="add" id="addBtn"></el-button>
		<el-button @click="remove(selectedRow)"></el-button>
		<el-button @click="update(selectedRow)"></el-button>
	</el-row>
	<el-table
	:data="industrializationInfoData"
	highlight-current-row
	border
	@row-click="handleSelectionChange"
	style="width: 100%">
		<el-table-column
		prop="declareTime"
		label=""
		></el-table-column>	
		<el-table-column
		prop="addNewConcrete"
		label=""
		></el-table-column>
		<el-table-column
		prop="addNewSteel"
		label=""
		></el-table-column>
		<el-table-column
		prop="addNewTimber"
		label=""
		></el-table-column>
	</el-table>
	<el-pagination
	@current-change="handlePageNumChange"
	@size-change="handlePageSizeChange"
    :current-page="pageNum"
    :page-size="pageSize"
    :page-sizes="[5,10,15,20]"
    layout="total, sizes, prev, pager, next, jumper"
    :total="totalIndustrializationInfoData">
	</el-pagination>
	<el-dialog v-model="showAddDialog" title="">		
		<el-form :model="addData" :rules="rules" ref="ruleForm">
			<el-form-item prop="declareTime">
				<el-col :span="5">
					
				</el-col>
				<el-col :span="7">
					<el-date-picker
				      v-model="addData.declareTime"
				      align="right"
				      type="date"
				      placeholder=""
				      :picker-options="pickerOptions1">
				    </el-date-picker>
				</el-col>
			</el-form-item>
			<el-form-item prop="year">
				<el-col :span="5">
					
				</el-col>
				<el-col :span="7">
					<el-input
					type='number'
					v-model="addData.year"
					align="right"
					></el-input>
				</el-col>
			</el-form-item>
			<el-form-item prop="quarter">
				<el-col :span="5">
					
				</el-col>
				<el-col :span="7">
					<el-input
					type='number'
					v-model="addData.quarter"
					align="right"
					max='4'
					min='1'
					></el-input>
				</el-col>
			</el-form-item>
			<el-form-item prop="addNewConcrete">
				<el-col :span="10">
					:
				</el-col>
				<el-col :span="14">
					<el-input v-model.number="addData.addNewConcrete" type='number' maxLength='5' placeholder="" min=0><template slot="append"></template></el-input>
				</el-col>	
			</el-form-item>
			<el-form-item prop="addNewSteel">
				<el-col :span="10">
					:
				</el-col>
				<el-col :span="14">
					<el-input v-model.number="addData.addNewSteel" type='number' placeholder="" min=0><template slot="append"></template></el-input>
				</el-col>
			</el-form-item>			
			<el-form-item prop="addNewTimber">
				<el-col :span="10">
					:
				</el-col>
				<el-col :span="14">
					<el-input v-model.number="addData.addNewTimber" type='number' placeholder="" min=0><template slot="append"></template></el-input>
				</el-col>
			</el-form-item>		
			<el-button type="primary" @click="submit('tempStore')"></el-button>
			<el-button type="primary" @click="submit('submit')"></el-button>
		</el-form>
	</el-dialog>
	<el-dialog v-model="showUpdateDialog" title="">	
		<el-form :model="updateData" :rules="rules" ref="ruleForm">
			<el-form-item prop="declareTime">
				<el-col :span="5">
					
				</el-col>
				<el-col :span="7">
					<el-date-picker
				      v-model="updateData.declareTime"
				      align="right"
				      type="date"
				      placeholder=""
				      :picker-options="pickerOptions1">
				    </el-date-picker>
				</el-col>
			</el-form-item>
			<el-form-item prop="year">
				<el-col :span="5">
					
				</el-col>
				<el-col :span="7">
					<el-input
					type='number'
					v-model="updateData.year"
					align="right"
					></el-input>
				</el-col>
			</el-form-item>
			<el-form-item prop="quarter">
				<el-col :span="5">
					
				</el-col>
				<el-col :span="7">
					<el-input
					type='number'
					v-model="updateData.quarter"
					align="right"
					max='4'
					min='1'
					></el-input>
				</el-col>
			</el-form-item>
			<el-form-item prop="addNewConcrete">
				<el-col :span="10">
					:
				</el-col>
				<el-col :span="14">
					<el-input v-model="updateData.addNewConcrete" type='number' maxLength='5' placeholder=""><template slot="append"></template></el-input>
				</el-col>	
			</el-form-item>
			<el-form-item prop="addNewSteel">
				<el-col :span="10">
					:
				</el-col>
				<el-col :span="14">
					<el-input v-model="updateData.addNewSteel" type='number' placeholder=""><template slot="append"></template></el-input>
				</el-col>
			</el-form-item>			
			<el-form-item prop="addNewTimber">
				<el-col :span="10">
					:
				</el-col>
				<el-col :span="14">
					<el-input v-model="updateData.addNewTimber" type='number' placeholder=""><template slot="append"></template></el-input>
				</el-col>
			</el-form-item>		
			<el-button type="primary" @click="cancelUpdate"></el-button>
			<el-button type="primary" @click="confirmUpdate('tempStore')"></el-button>
			<el-button type="primary" @click="confirmUpdate('submit')"></el-button>
		</el-form>
	</el-dialog>
	<msg-dialog ref="msgDialog"></msg-dialog>
</div>
</template>
<script type="text/javascript">
import moment from 'moment'
import msgDialog from '../../../components/common/msgDialog.vue'
	export default{
		data:function(){
			var validateNumber = (rule, value, callback) => {
		        if (value==='') {
		          return callback(new Error('0'))
		        }else if (value<0) {
		        	return callback(new Error('0'))
		        } else{
		        	callback()
		        } 
	      	}
			return{
				addData:{
					//
					addNewConcrete:'',
					//
	    			addNewSteel:'',
	    			// 
	    			addNewTimber:'',
	    			//
	    			submit:'',
	    			declareTime:'',
	    			year:'',
	    			quarter:''
				},			
    			//
    			updateData:{
    				id:'',
    				declareTime:'',
    				addNewConcrete:'',
    				addNewSteel:'',
    				addNewTimber:'',
    				year:'',
    				quarter:'',
    				checkedStatus:'',
    				submit:''
    			},
    			showAddDialog:false,
    			showUpdateDialog:false,
				selectedRow:'',
				selectedRowId:'',
				//
				totalIndustrializationInfoData:'',
				//
				industrializationInfoData:'',
				//id.
				checkedStatus:'',
				//
				isSubmit:'',
				pageSize:10,
				pageNum:1,
				pickerOptions1: {
					//
					disabledDate(time) {
		            	return time.getTime() > Date.now()+ 24*60*60*1000 - 8.64e7;
		          	},
		          	//
		         	shortcuts: [{
		            	text: '',
		            	onClick(picker) {
		             		picker.$emit('pick', new Date());
		            	}
		          	}, {
		            	text: '',
		            	onClick(picker) {
		              		const date = new Date();
		              		date.setTime(date.getTime() - 3600 * 1000 * 24);
		              		picker.$emit('pick', date);
		            	}
		          	}, {
		            	text: '',
		            	onClick(picker) {
		              		const date = new Date();
		              		date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
		              		picker.$emit('pick', date);
		            	}
		          	}]
		        },
		        rules:{
		        	//
					addNewConcrete:[{validator:validateNumber,trigger: 'change',message:''}],
					//
	    			addNewSteel:[{validator:validateNumber,trigger: 'change',message:''}],
	    			// 
	    			addNewTimber:[{validator:validateNumber,trigger: 'change',message:''}],
	    			declareTime:[{validator:validateNumber,trigger: 'change',message:''}],
	    			year:[{validator:validateNumber,trigger: 'change',message:''}],
	    			quarter:[{validator:validateNumber,trigger: 'change',message:''}],
		        }
			}
		},
		created:function(){
			var currentDate= new Date()
			var currMonth=currentDate.getMonth()
			var preDate =new Date(currentDate.getTime()- 24*60*60*1000)
			this.addData.declareTime=preDate
			this.addData.year=currentDate.getFullYear()
			this.addData.quarter=Math.floor( ( currMonth % 3 == 0 ? ( currMonth / 3 ) : ( currMonth / 3 + 1 ) ) )
			this.getAllIndustrializationInfo()			
		},
		methods:{
			getAllIndustrializationInfo:function(){
				var url=this.HOST+'/getAllConstructionEnIndustrializations?page='+this.pageNum+'&rows='+this.pageSize
				this.$http.get(url).then(response=>{
					this.industrializationInfoData=response.data.rows
					this.totalIndustrializationInfoData=response.data.total
				}).catch(response=>{
					this.$refs.msgDialog.confirm("!")
				})
			},
			handleSelectionChange:function(val){
				this.selectedRow=val
				this.selectedRowId=val.id
				this.checkedStatus=val.checkedStatus
				this.isSubmit=val.submit
			},
			handlePageNumChange:function(val){
				this.pageNum=val
				this.getAllIndustrializationInfo()
			},
			handlePageSizeChange:function(val){
				this.pageSize=val
				this.getAllIndustrializationInfo()
			},
			add:function(){
				this.showAddDialog=true
			},
			//
			submit:function(val){
				if (val=='tempStore') {
					this.addData.submit=false
				}else{
					this.addData.submit=true
				}				
				this.$refs.ruleForm.validate((valid)=>{
					if (valid) {
						var url=this.HOST+'/addConstructionEnIndustrialization'
						this.$http.post(url,this.addData).then(response=>{					
							this.getAllIndustrializationInfo()
							this.showAddDialog=false	
							this.addData.addNewTimber=''
							this.addData.addNewConcrete=''
							this.addData.addNewSteel=''
							this.addData.submit=''
							this.$refs.msgDialog.notify("")
						}).catch(response=>{
							if (response=='Error: Request failed with status code 500') {
								this.$refs.msgDialog.confirm("")
							}
						})			
					}else{
						this.$refs.msgDialog.confirm(",")
		            	return false;
					}
				})
								
			},
			update:function(val){
				if (val) {
					if (this.isSubmit==true) {
						if (this.checkedStatus==null || this.checkedStatus.id==1 || this.checkedStatus.id==3) {
							this.$refs.msgDialog.confirm("")
						}else{
							this.showUpdateDialog=true
							this.updateData.id=val.id
							this.updateData.addNewConcrete=val.addNewConcrete
							this.updateData.addNewSteel=val.addNewSteel
							this.updateData.addNewTimber=val.addNewTimber
							this.updateData.declareTime=val.declareTime
							this.updateData.year=val.year
							this.updateData.quarter=val.quarter
							this.updateData.checkedStatus=val.checkedStatus
						}
					}else{
						this.showUpdateDialog=true
						this.updateData.id=val.id
						this.updateData.addNewConcrete=val.addNewConcrete
						this.updateData.addNewSteel=val.addNewSteel
						this.updateData.addNewTimber=val.addNewTimber
						this.updateData.declareTime=val.declareTime
						this.updateData.year=val.year
						this.updateData.quarter=val.quarter
						this.updateData.checkedStatus=val.checkedStatus				
					}				
				}else{
					this.$refs.msgDialog.confirm("")
				}
			},
			confirmUpdate:function(val){
				if (val=='tempStore') {
					this.updateData.submit=false
				}else{
					this.updateData.submit=true
				}			
				this.$refs.ruleForm.validate((valid)=>{
					if (valid) {
						var url=this.HOST+'/updateConstructionEnIndustrialization'
						this.$http.put(url,this.updateData).then(response=>{
							this.$refs.msgDialog.notify("")
							this.getAllIndustrializationInfo()
							this.showUpdateDialog=false
						}).catch(response=>{
							this.$refs.msgDialog.notify("")
							this.showUpdateDialog=false
						})		
					}else{
						this.$refs.msgDialog.confirm(",")
		            	return false;
					}
				})
				
			},
			cancelUpdate:function(){
				this.showUpdateDialog=false
				this.updateData.addNewConcrete=''
				this.updateData.addNewSteel=''
				this.updateData.addNewTimber=''
				this.updateData.declareTime=''
				this.updateData.checkedStatus=''
			},
			remove:function(val){
				if (val) {
					if (this.isSubmit==true) {
						if (this.checkedStatus==null || this.checkedStatus.id==1 || this.checkedStatus.id==3) {
							this.$refs.msgDialog.confirm("")
						}else{
							var url=this.HOST+'/deleteConstructionEnIndustrialization?id='+this.selectedRowId
							this.$confirm(', ?', '', {
					          confirmButtonText: '',
					          cancelButtonText: '',
					          type: 'warning'
					        }).then(()=>{
					        	this.$http.delete(url).then(response=>{
									this.$refs.msgDialog.notify("")
									this.getAllIndustrializationInfo()
								}).catch(response=>{
									this.$refs.msgDialog.notify("")
								})
					        }).catch(()=>{
					        	this.$refs.msgDialog.notify("")
					        })					
						}							
					}else{
						var url=this.HOST+'/deleteConstructionEnIndustrialization?id='+this.selectedRowId
							this.$confirm(', ?', '', {
					          confirmButtonText: '',
					          cancelButtonText: '',
					          type: 'warning'
					        }).then(()=>{
					        	this.$http.delete(url).then(response=>{
									this.$refs.msgDialog.notify("")
									this.getAllIndustrializationInfo()
								}).catch(response=>{
									this.$refs.msgDialog.notify("")
								})
					        }).catch(()=>{
					        	this.$refs.msgDialog.notify("")
					        })				
					}
				}else{
					this.$refs.msgDialog.confirm("")
				}
			},
		},
		components:{
			msgDialog
		}
	}
</script>
<style type="text/css">
	#body{
		width: 100%;
		height: 100%
	}
	#header{
		width: 100%;
		height: 10%;
		float: left;
	}
	
</style>
	


