<template></template>
<script>
	export default{
		methods:{
			//
			confirm(msg) {
                this.$alert(msg,"", {
                  confirmButtonText: ''
                })
            },
            //
		    notify(msg) {
		        const h = this.$createElement;
		        this.$message({
		          message: h('p', null, [
		            h('span', null, msg),
		            h('i', { style: 'color: teal' }, '')
		          ])
		        });
		    }

		}
	}
</script>


