<template>
	<el-form>
		<el-form-item label="">
	        <el-input v-model="unitEngineeringDialog.name" :disabled="true">
	        </el-input>
	    </el-form-item>
	    <el-form-item label="">
	        <el-input v-model="unitEngineeringDialog.constructionArea":disabled="true">
	        </el-input>
	    </el-form-item>
	    <el-form-item label="">
	        <el-input v-model="unitEngineeringDialog.undergroundNum":disabled="true">
	        </el-input>
	    </el-form-item>
	    <el-form-item label="">
	        <el-input v-model="unitEngineeringDialog.overgroundNum":disabled="true">
	        </el-input>
	    </el-form-item>
	    <el-form-item label="">
	        <el-input v-model="unitEngineeringDialog.engineeringCategory":disabled="true">
	        </el-input>
	    </el-form-item>
	    <el-form-item label="">
	    	<el-select v-model="unitEngineeringDialog.structureForm.id" placeholder="":disabled="true">
	            <el-option
	              v-for="item in structureForms"
	              :key="item.id"
	              :label="item.description"
	              :value="item.id">
	            </el-option>    
	        </el-select>
	    </el-form-item>
	    <el-form-item label="">
	        <el-input v-model="unitEngineeringDialog.engineeringIndustrialization.unitAssemblyRate":disabled="true">
	        </el-input>
	    </el-form-item>
	    <el-form-item label="">
	        <el-input v-model="unitEngineeringDialog.engineeringIndustrialization.exteriorWallArea":disabled="true">
	        </el-input>
	    </el-form-item>
	    <el-form-item label="">
	        <el-input v-model="unitEngineeringDialog.engineeringIndustrialization.wallShadowArea":disabled="true">
	        </el-input>
	    </el-form-item>
	    <el-form-item label="">
	        <el-input v-model="unitEngineeringDialog.engineeringIndustrialization.conArea":disabled="true">
	        </el-input>
	    </el-form-item>
	    <el-form-item label="">
	    	<el-select v-model="unitEngineeringDialog.engineeringIndustrialization.applicationStructureType.id" placeholder="":disabled="true">
	            <el-option
	              v-for="item in applicationStructureTypes"
	              :key="item.id"
	              :label="item.description"
	              :value="item.id">
	            </el-option>    
	        </el-select>       
	    </el-form-item>
	    <el-form-item label="">
	        <el-input v-model="unitEngineeringDialog.engineeringIndustrialization.floorScope":disabled="true">
	        </el-input>
	    </el-form-item>
	    <el-form-item label="">
	        <el-checkbox label="" v-model="unitEngineeringDialog.engineeringIndustrialization.frameworkShear.columnFs":disabled="true"></el-checkbox>
		    <el-checkbox label="" v-model="unitEngineeringDialog.engineeringIndustrialization.frameworkShear.beamFs":disabled="true"></el-checkbox>
		    <el-checkbox label="" v-model="unitEngineeringDialog.engineeringIndustrialization.frameworkShear.floorFs":disabled="true"></el-checkbox>
		    <el-checkbox label="" v-model="unitEngineeringDialog.engineeringIndustrialization.frameworkShear.stairsFs":disabled="true"></el-checkbox>
		    <el-checkbox label="" v-model="unitEngineeringDialog.engineeringIndustrialization.frameworkShear.exteriorWallFs":disabled="true"></el-checkbox>
		    <el-checkbox label="" v-model="unitEngineeringDialog.engineeringIndustrialization.frameworkShear.interiorWallFs":disabled="true"></el-checkbox>
		    <el-checkbox label="" v-model="unitEngineeringDialog.engineeringIndustrialization.frameworkShear.integralKitchenFs":disabled="true"></el-checkbox>
		    <el-checkbox label="" v-model="unitEngineeringDialog.engineeringIndustrialization.frameworkShear.integralToiletFs":disabled="true"></el-checkbox>
		    <el-checkbox label="" v-model="unitEngineeringDialog.engineeringIndustrialization.frameworkShear.solarEnergyFs":disabled="true"></el-checkbox>
	    </el-form-item>
	</el-form>
</template>
<script>
	export default{
		data(){
      		return{
        		applicationStructureTypes:[],
        		structureForms:[]
      		}
    	},
    	methods: {
    		findAllApplicationStructureTypes() {
	        	this.$http.get(this.HOST + "/findAllApplicationStructureTypes").then(response => {
	          	this.applicationStructureTypes = response.data;
	        	}).catch(error => {
	          	this.$refs.msgDialog.confirm("")
	        	})
	      	},
	       	findAllStructureForms() {
        		this.$http.get(this.HOST + "/findAllStructureForms").then(response => {
          		this.structureForms = response.data;
        		}).catch(error => {
          		this.$refs.msgDialog.confirm("")
        		})
      		}
    	},
		props:['unitEngineeringDialog'],
		created() {
      		this.findAllApplicationStructureTypes()
      		this.findAllStructureForms()
    	},
	}
</script>


