<template>
<div>
	<div id="header">
		<h4>
			<label></label>
			<label></label>
		</h4>	
	</div>
	<el-row>
		<el-col :span="6">
			<el-input
			  placeholder=""
			  icon="search"
			  v-model="selectedCompanyName"
			  :on-icon-click="searchTraditionalInfo">
			</el-input>			
		</el-col>
		<el-col :span="3">
			
		</el-col>
		<el-col :span="6">
			<el-date-picker
		      v-model="searchTime"
		      type="daterange"
		      placeholder="">
		    </el-date-picker>		
		</el-col>
		<el-col :span="6">
			<el-button type="primary" @click="searchTraditionalInfo"></el-button>
			<el-button @click="passCheck(selectedId)"></el-button>
			<el-button @click="failCheck(selectedId)"></el-button>	
		</el-col>	
	</el-row>
	<el-col :span="6">	
		<el-row>
			<el-table
			:data="companyInfoData"
			highlight-current-row
			border
			@row-click="handleRowChange">
			<el-table-column
			label=""
			prop="componentEn.name">				
			</el-table-column>
			<el-table-column
			label=""
			prop="declareTime">				
			</el-table-column>
			</el-table>
			<el-pagination
				@current-change="handlePageNumChange"
				@size-change="handlePageSizeChange"
				:page-sizes="[5,10,15,20]"
			    :current-page="pageNum"
			    :page-size="pageSize"
			    layout="total,  prev, pager, next, jumper"
			    :total="total">
			</el-pagination>
		</el-row>	
	</el-col>
	<el-col :span="18">
		<el-col :span="24">	
			<el-row>
				<el-col :span="6">
					
				</el-col>
				<el-col :span="6">
					
				</el-col>
				<el-col :span="6">
					
				</el-col>
				<el-col :span="6">
					
				</el-col>
			</el-row>		
			<el-row>
				<el-col :span="6">
					
				</el-col>				
				<el-col :span="6"><el-input v-model="this.selectedRow.prebuiltConcreteNum" disabled></el-input></el-col>
				<el-col :span="6"><el-input v-model="this.selectedRow.prebuiltConcreteAbility" disabled><template slot="append"></template></el-input></el-col>
				<el-col :span="6"><el-input v-model="this.selectedRow.prebuiltConcreteScale" disabled><template slot="append"></template></el-input></el-col>		
			</el-row>
			<el-row>
				<el-col :span="6">"</el-col>
				<el-col :span="6"><el-input v-model="this.selectedRow.prebuiltSteelNum" disabled></el-input></el-col>
				<el-col :span="6"><el-input v-model="this.selectedRow.prebuiltSteelAbility"disabled><template slot="append"></template></el-input></el-col>
				<el-col :span="6"><el-input v-model="this.selectedRow.prebuiltSteelScale" disabled><template slot="append"></template></el-input></el-col>	
			</el-row>
			<el-row>
				<el-col :span="6"></el-col>
				<el-col :span="6"><el-input v-model="this.selectedRow.prebuiltTimberNum" disabled></el-input></el-col>
				<el-col :span="6"><el-input v-model="this.selectedRow.prebuiltTimberAbility" disabled><template slot="append"></template></el-input></el-col>
				<el-col :span="6"><el-input v-model="this.selectedRow.prebuiltTimberScale" disabled><template slot="append"></template></el-input></el-col>	
			</el-row>
			<el-row>
				<el-col :span="6"></el-col>
				<el-col :span="6"><el-input v-model="this.selectedRow.prebuiltOtherNum" disabled></el-input></el-col>
				<el-col :span="6"><el-input v-model="this.selectedRow.prebuiltOtherAbility" disabled><template slot="append"></template></el-input></el-col>
				<el-col :span="6"><el-input v-model="this.selectedRow.prebuiltOtherScale" disabled><template slot="append"></template></el-input></el-col>	
			</el-row>
		</el-col>
	</el-col>
	<msg-dialog ref="msgDialog"></msg-dialog>
</div>
	
</template>
<script type="text/javascript">
import moment from 'moment'
import msgDialog from '../common/msgDialog.vue'
	export default{
		data(){
			return{
				companyInfoData:'',
				selectedCompanyName:'',
				searchTime:'',
				pageNum:1,
				pageSize:10,
				total:'',
				selectedRow:'',
				selectedComponentEnId:'',
				selectedId:'',
			}
		},
		created:function(){
			this.getComponentInfoData()
		},
		methods:{
			getComponentInfoData:function(){
				var url = this.HOST+'/displayAllComponentEnIndustrializationsBySubmit?page='+this.pageNum+'&rows='+this.pageSize
				this.$http.get(url).then(response=>{
					this.companyInfoData=response.data.rows
					this.total=response.data.total
				}).catch(response=>{
					this.$refs.msgDialog.notify("")
				})
			},
			searchTraditionalInfo:function(){
				if (this.selectedCompanyName==''||this.searchTime=='') {
					this.$refs.msgDialog.confirm("")
				}else{
					var startTime=moment(this.searchTime[0]).format("YYYY-MM-DD")
					var endTime=moment(this.searchTime[1]).format("YYYY-MM-DD")
					var url=this.HOST+'/queryComponentEnIn?componentEnName='+this.selectedCompanyName+'&startTime='+startTime+'&endTime='+endTime+'&page='+this.pageNum+'&rows='+this.pageSize
					this.$http.get(url).then(response=>{
						this.companyInfoData=response.data.rows
						this.total=response.data.total
					}).catch(response=>{
						this.$refs.msgDialog.notify("")
					})
				}
			},
			handleRowChange:function(val){
				this.selectedRow=val
				this.selectedId=val.id				
				this.selectedComponentEnId=val.componentEn.id
			},
			handlePageNumChange:function(val){
				this.pageNum=val
				this.getComponentInfoData()
			},
			handlePageSizeChange:function(val){
				this.pageSize=val
				this.getComponentInfoData()
			},
			failCheck:function(val){
				if (val) {
					var url=this.HOST+'/checkComponentEnIndustrialization?id='+this.selectedId+'&constructionEnId='+this.selectedComponentEnId+'&checkedStatusId='+3
					this.$http.post(url).then(response=>{
						this.getComponentInfoData()	
						this.selectedId=''			
						this.$refs.msgDialog.notify("")
					}).catch(response=>{
						this.$refs.msgDialog.notify("")
					})
				}else{

					this.$refs.msgDialog.confirm("")				
				}
				
			},
			passCheck:function(val){
				// /checkComponentEnIndustrialization
				if (val) {
					var url=this.HOST+'/checkComponentEnIndustrialization?id='+this.selectedId+'&constructionEnId='+this.selectedComponentEnId+'&checkedStatusId='+1
					this.$http.post(url).then(response=>{
						this.getComponentInfoData()				
						this.selectedId=''
						this.$refs.msgDialog.notify("")
					}).catch(response=>{
						this.$refs.msgDialog.notify("")
					})
				}else{
					this.$refs.msgDialog.confirm("")
				}
				
			}
		},
		components:{
			msgDialog
		}
	}
</script>
<style type="text/css">
	#header{
		width: 100%;
		height: 5%;
		margin-top: -30px
	}
</style>


