<template>
	<!--  -->
    <el-dialog title="" :visible.sync="submitBasicInfoDialogVisible">
        <el-row>
            <div>
                <label></label>
            </div>
        </el-row>
        <div slot="footer">
            <el-button @click="cancle"> </el-button>

            <el-button type="primary" @click="handleSubmit"> </el-button>
        </div>
    </el-dialog>
</template>

<script>
	export default{
		props:['submitBasicInfoDialogVisible'],
		methods:{
			handleSubmit(){
				this.$emit('handleSubmit')
			},
			cancle(){
				this.$emit('cancle')
			}
		}
	}
</script>


