<template>
<div id="header">
    <div class="headerContent">
    	<p style="color:#8492A6"></p>
    </div>
    <div style="width:20%;float:left;padding-top:15px">
    	<p>{{ "" + currentUser}}</p>
    </div>
    <!-- <div style="float:margin-right;padding-top:22px">
      <el-button type="primary" @click="logout"></el-button>
    </div> -->
</div>
</template>

<script>
export default{
     data() {
      return {
        currentUser: ''
      }
    },
    methods: {
      //
      findUser() {
        //
        // 
        this.$http.get(this.HOST + "/userInfo").then(response => {
          this.currentUser = response.data.userType;
        }).catch(error => {
          
        })
      },
      logout() {
        //
        // 
        //window.location.href="http://localhost:8080/absmis/login"
      }

    },
    //watch
    watch: {},
    //
    created() {
      this.findUser()
    }
} 
</script>
<style type="text/css">
	#header{
		width: 100%;
		/* margin: 0 auto; */
	}
  .headerContent{
    font-size: 29px;
    /*font-family: "Times New Roman",Georgia,Serif;*/
    font-family: "Arial","Microsoft YaHei","","",sans-serif;
    color: #000000;
    width: 70%;
    float: left;
    /*padding-top: -28px !important;*/
    padding-left: 5px !important;
    text-align: left;
    line-height: 30px;
  }
</style>



