<template>
	<div>
		<el-row>
			<el-col :span="4">
				<el-input v-model="searchContent" placeholder=""></el-input>
			</el-col>
			<el-col :span="3">
				<el-button @click="searchDesignTraditionalData"></el-button>
			</el-col>
			<el-col :span="3">
				<el-button @click="check"></el-button>
			</el-col>
		</el-row>
		<el-table
		    :data="traditionalCheckTableData"
		    border
		    tooltip-effect="dark"
		    style="width:100%"
		    
		    highlight-current-row
		    @row-click="handleSelectionChange">
		    <el-table-column
		      label=""
		      prop="name"
		      show-overflow-tooltip>
		    </el-table-column>

		    <el-table-column
		      label=""
		      prop="enterpriseType"
		      show-overflow-tooltip>
		    </el-table-column>

		    <el-table-column
		      label=""
		      prop="creditCode"
		      show-overflow-tooltip>
		    </el-table-column>

		    <el-table-column
		      label=""
		      prop="organizationCode"
		      show-overflow-tooltip>
		    </el-table-column>

		    <el-table-column
		      label=""
		      prop="city"
		      show-overflow-tooltip>
		    </el-table-column>

		    <el-table-column
		      label=""
		      prop="legalRepresentative"
		      show-overflow-tooltip>
		    </el-table-column>

		    <el-table-column
		      label=""
		      prop="qualificationNo"
		      show-overflow-tooltip>
		    </el-table-column>

		    <el-table-column
		      label=""
		      prop="designerQualification.description"
		      show-overflow-tooltip>
		    </el-table-column>

		    <el-table-column
		      label=""
		      prop="checkedStatus.state"
		      show-overflow-tooltip>
		    </el-table-column>
	  	</el-table>
	  	<!--  -->
	  	<el-pagination
	      @size-change="handleSizeChange"
	      @current-change="handleCurrentChange"
	      :current-page="currentPage"
	      :page-sizes="[5, 10, 15, 20]"
	      :page-size="pageSize"
	      layout="total, sizes, prev, pager, next, jumper"
	      :total="total">
	    </el-pagination>
	  	<msg-dialog ref="traditionalCheckMsg"></msg-dialog>
	  	<!--  -->
	  	<el-dialog
		  	title=""
		  	:visible.sync="checkedStatusDialogVisible"
		  	size="tiny">
		  	<el-select v-model="checkedStatusId" placeholder="">
			    <el-option
			      v-for="item in checkedStatus"
			      :key="item.id"
			      :label="item.state"
			      :value="item.id">
			    </el-option>
			 </el-select>
		  	<span slot="footer" class="dialog-footer">
		    <el-button @click="checkedStatusDialogVisible = false"> </el-button>
		    <el-button type="primary" @click="checkedStatusUpdate"> </el-button>
		  </span>
		</el-dialog>
	</div>
</template>

<script>
import msgDialog from '../common/msgDialog'
	export default{
		data(){
			return{
				//
				traditionalCheckTableData:[],
				//
				checkedStatusDialogVisible:false,
				//
				checkedStatus:'',
				//id
				checkedStatusId:'',
				//id
				currentRowId:'',
				//
				currentPage:1,
				//
				pageSize:5,
				//
				total:0,
				//
				searchContent:'',
				//
				searchContentFinal:''

			}
		},
		//
		created(){
			//
			this.findDesignTraditionalInfo()
			//
			var url = this.HOST + '/findAllCheckedStates'
			this.$http.get(url).then(response=>{
				this.checkedStatus = response.data
			})
		},
		methods:{
			//
			handleSizeChange(newPageSize){
				this.pageSize=newPageSize
				this.findDesignTraditionalInfo()
			},
			//
			handleCurrentChange(newPage){
				this.currentPage=newPage
				this.findDesignTraditionalInfo()
			},
			//
			findDesignTraditionalInfo(){
				var url = this.HOST + '/displayAllDesigners?page='+this.currentPage+"&rows="+this.pageSize
				this.$http.get(url).then(response=>{
					this.traditionalCheckTableData = response.data.rows
					this.total=response.data.total
					//
					this.traditionalCheckTableData.forEach(item=>{
						if(!item.checkedStatus){
							item.checkedStatus={id:'',no:'',state:""}
						}
					})
				}).catch(error=>{
					this.$refs.traditionalCheckMsg.confirm("")
				})
			},
			//
			check(){
				if(this.currentRowId!=''){
					this.checkedStatusDialogVisible=true
				}else{
					this.$refs.traditionalCheckMsg.confirm("")
				}
			},
			//
			checkedStatusUpdate(){
				this.checkedStatusDialogVisible=false
				var url=this.HOST+"/checkDesigner?id="+this.currentRowId+"&checkedStatusId="+this.checkedStatusId
				this.$http.post(url).then(response=>{
					if(this.searchContentFinal){
						var url = this.HOST + "/queryDesignerByName?nameQuery="+this.searchContentFinal+"&rows="+this.pageSize+"&page="+this.currentPage
						this.$http.get(url).then(response=>{
							this.traditionalCheckTableData = response.data.rows
							this.total=response.data.total
							//
							this.traditionalCheckTableData.forEach(item=>{
								if(!item.checkedStatus){
									item.checkedStatus={id:'',no:'',state:""}
								}
							})
						}).catch(error=>{
							this.$refs.traditionalCheckMsg.confirm("")
						})
					}else{
						this.findDesignTraditionalInfo()
					}
					this.$refs.traditionalCheckMsg.notify("")
				}).catch(error=>{
					this.$refs.traditionalCheckMsg.confirm("")
				})
				this.checkedStatusId=''
				this.currentRowId=''
			},
			//
			handleSelectionChange(currentRow){
				//id
				this.currentRowId=currentRow.id
				//id
				this.checkedStatusId=currentRow.checkedStatus.id
			},
			//
			searchDesignTraditionalData(){
				this.searchContentFinal = this.searchContent
				var url = this.HOST + "/queryDesignerByName?nameQuery="+this.searchContentFinal+"&rows="+this.pageSize+"&page="+this.currentPage
				this.$http.get(url).then(response=>{
					this.traditionalCheckTableData = response.data.rows
					this.total=response.data.total
					//
					this.traditionalCheckTableData.forEach(item=>{
						if(!item.checkedStatus){
							item.checkedStatus={id:'',no:'',state:""}
						}
					})
				}).catch(error=>{
					this.$refs.traditionalCheckMsg.confirm("")
				})
			}
		},
		//
		components:{
			msgDialog
		}
	}
</script>


